<template>
    <div>
        <video id="localVideo" autoplay muted></video>
        <video id="remoteVideo" autoplay></video>
    </div>
</template>

<script>
import io from 'socket.io-client';

export default {
    name: 'WebRTCComponent',
    data() {
        return {
            isChannelReady: false,
            isInitiator: false,
            isStarted: false,
            localStream: null,
            pc: null,
            remoteStream: null,
            turnReady: null,
            pcConfig: {
                iceServers: [
                    { urls: "stun:stun.l.google.com:19302" },
                    { urls: "stun:stun1.l.google.com:19302" },
                    {
                        urls: 'turn:your-turn-server.com:3478',
                        credential: 'your-turn-server-password',
                        username: 'your-turn-server-username'
                    }
                ],
            },
            room: 'foo',
            socket: null,
        };
    },
    mounted() {
        this.socket = io.connect();

        if (this.room !== '') {
            this.socket.emit('create or join', this.room);
            console.log('방 생성 혹은 참여 시도: ', this.room);
        }

        this.socket.on('created', (room) => {
            console.log('방 생성됨: ' + room);
            this.isInitiator = true;
        });

        navigator.mediaDevices
            .getUserMedia({
                audio: false,
                video: true,
            })
            .then(stream => this.gotStream(stream))
            .catch((e) => {
                alert('getUserMedia() 에러: ' + e.name);
            });
    },
    methods: {
        gotStream(stream) {
            console.log('로컬 스트림 추가.');
            this.localStream = stream;
            var localVideo = document.getElementById('localVideo');
            localVideo.srcObject = stream;
            this.sendMessage('got user media');
            if (this.isInitiator) {
                this.maybeStart();
            }
        },
        sendMessage(message) {
            console.log('클라이언트 메시지 전송: ', message);
            this.socket.emit('message', message);
        },
        maybeStart() {
            console.log('maybeStart() 호출 ', this.isStarted, this.localStream, this.isChannelReady);
            if (!this.isStarted && typeof this.localStream !== 'undefined' && this.isChannelReady) {
                this.createPeerConnection();
                this.pc.addStream(this.localStream);
                this.isStarted = true;
                if (this.isInitiator) {
                    this.doCall();
                }
            }
        },
        createPeerConnection() {
            try {
                this.pc = new RTCPeerConnection(this.pcConfig);
                this.pc.onicecandidate = (event) => this.handleIceCandidate(event);
                this.pc.onaddstream = (event) => this.handleRemoteStreamAdded(event);
                this.pc.onremovestream = (event) => this.handleRemoteStreamRemoved(event);
                console.log('RTCPeerConnnection 생성됨');
            } catch (e) {
                console.log('PeerConnection 생성 실패, 예외: ' + e.message);
                alert('RTCPeerConnection 객체를 생성할 수 없습니다.');
                return;
            }
        },
        handleIceCandidate(event) {
            console.log('icecandidate 이벤트: ', event);
            if (event.candidate) {
                this.sendMessage({
                    type: 'candidate',
                    label: event.candidate.sdpMLineIndex,
                    id: event.candidate.sdpMid,
                    candidate: event.candidate.candidate,
                });
            } else {
                console.log('후보 끝.');
            }
        },
        doCall() {
            console.log('피어에게 오퍼 전송 중');
            this.pc.createOffer(this.setLocalAndSendMessage, function (error) {
                console.log('오퍼 생성 실패: ', error);
            });
        },
        doAnswer() {
            console.log('피어에게 응답 전송 중.');
            this.pc.createAnswer().then(
                this.setLocalAndSendMessage,
                function (error) {
                    console.log('세션 설명 생성 실패: ' + error.toString());
                }
            );
        },
        setLocalAndSendMessage(sessionDescription) {
            this.pc.setLocalDescription(sessionDescription);
            console.log('setLocalAndSendMessage 메시지 전송', sessionDescription);
            this.sendMessage(sessionDescription);
        },
        handleRemoteStreamAdded(event) {
            console.log('원격 스트림 추가됨.');
            this.remoteStream = event.stream;
            var remoteVideo = document.getElementById('remoteVideo');
            remoteVideo.srcObject = this.remoteStream;
        },
        handleRemoteStreamRemoved(event) {
            console.log('원격 스트림 제거됨. 이벤트: ', event);
        },
        handleRemoteHangup() {
            console.log('세션 종료됨.');
            this.stop();
            this.isInitiator = false;
        },
        stop() {
            this.isStarted = false;
            this.pc.close();
            this.pc = null;
        },
    },
};

</script>
